.dropshadow {
    filter: drop-shadow(0px 16px 24px rgba(169,80,196,0.25));
}
.bg-icon-mobile {
    &::after {
      background-image: url("../svg/icon-mobile.svg");
      background-size: cover;
      transition: 0.5s ease;
      cursor: pointer;
      filter: drop-shadow(0px 16px 24px rgba(169, 80, 196, 0.25));
    }
  
    &:hover {
      &::after {
        transform: scale(1.05);
      }
    }
  }
  
